/**
* Layout
*/
.calendar-container {
  width: 100%; }

.days-view, .months-view, .years-view {
  width: 100%;
  font-size: 0; }

.days-view__day-container, .months-view__month-container, .years-view__year-container {
  width: 14.28%;
  display: inline-block;
  height: 3rem;
  position: relative; }

.months-view__month-container, .years-view__year-container {
  width: 33.33%;
  height: 4rem; }

.days-view__day, .months-view__month, .years-view__year {
  position: relative; }

.calendar-control {
  display: flex;
  justify-content: space-between; }

.calendar-control__button-selector {
  flex-grow: 1; }

/**
* Theme
*/
.days-view, .months-view, .years-view {
  border: solid 1px #ddd; }

.days-view__day-container, .months-view__month-container, .years-view__year-container {
  background-color: aliceblue;
  color: black;
  border: solid 1px #878787;
  cursor: pointer; }

.days-view__day-container:hover {
  border: solid 1px #333; }

.days-view__day-container--disabled {
  background-color: #fafdff;
  cursor: default;
  color: #aaa;
  border: solid 1px #c3c3c3; }
  .days-view__day-container--disabled:hover {
    border: solid 1px #c3c3c3; }

.days-view__day, .months-view__month, .years-view__year {
  font-size: 1.3rem;
  text-align: center;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%); }

.days-view__day--before, .days-view__day--after {
  background-color: darkgray; }
  .days-view__day--before.days-view__day-container--disabled, .days-view__day--after.days-view__day-container--disabled {
    background-color: #cfcfcf; }

.days-view__day-container--selected {
  background-color: #ddd; }

.ac-calendar--float {
  width: 24rem; }

/**
* Layout
*/
.ac-modal-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow: hidden; }

.ac-modal-content-container {
  position: absolute;
  margin: 0;
  display: flex;
  flex-direction: column;
  top: 10vh;
  max-height: 80vh;
  width: 98%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  overflow: auto; }

.ac-modal--extra-small .ac-modal-content-container {
  width: 49%; }

/* Small Devices, Desktops */
@media only screen and (min-width: 400px) {
  .ac-modal--small .ac-modal-content-container {
    width: 60%; }
  .ac-modal--extra-small .ac-modal-content-container {
    width: 30%; } }

.ac-modal-content {
  -webkit-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  -o-user-select: text;
  user-select: text; }

.ac-modal-body {
  flex-grow: 1;
  overflow-y: auto;
  overflow-x: hidden; }

.ac-modal-icon {
  position: relative;
  top: 0.5px;
  margin-right: 0.3rem; }

/* Medium Devices, Desktops */
@media only screen and (min-width: 992px) {
  .ac-modal-content-container {
    top: 15vh;
    max-height: 70vh;
    width: 80%; }
  .ac-modal--small .ac-modal-content-container {
    width: 40%; }
  .ac-modal--extra-small .ac-modal-content-container {
    width: 20%; } }

/* Large Devices, Wide Screens */
@media only screen and (min-width: 1200px) {
  .ac-modal-content-container {
