.ac-accordion__header, .ac-accordion__body {
  padding: 0.5rem 1rem; }

.ac-accordion__header {
  position: relative; }

.ac-accordion__right-icon {
  position: absolute;
  right: 0.6rem;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%); }

.accordion-wrapper .ac-accordion-container {
  margin-bottom: 0.4rem; }

/**
* Theme
*/
.ac-accordion__header {
  background-color: #ddd;
  cursor: pointer; }

.ac-accordion__header--locked {
  cursor: initial; }

.ac-accordion-container {
  border: solid 1px #aaa; }

.ac-accordion__right-icon {
  cursor: pointer; }

.with-errors .ac-accordion-container {
  border-color: #a94442; }

.select-checkbox-container, .select-label-container {
  float: left; }

.select-item-container {
  display: block;
  margin-bottom: 0.75rem;
  border-bottom: solid 1px #ddd; }

.select-item-container:last-child {
  border-bottom: none;
  margin-bottom: 0; }

.select-checkbox-container {
  margin-right: 0.5rem; }

.select-checkbox-container [type='checkbox'] {
  margin: 0; }

.select-search {
  position: relative; }

.select-search-menu {
  position: absolute;
  background-color: white;
  left: 0;
  top: 100%;
  width: 100%;
  border: solid 1px lightgray;
  border-radius: 3px;
  z-index: 2002; }

.select-search__inside {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

.select-search__busqueda {
  padding: 1em 0.5em 0 0.5em; }

.select-search__items {
  cursor: initial;
  max-height: 10em;
  overflow-y: scroll; }

.add-select-search {
  min-height: 11em; }

.select-search__item {
  padding: 0.5em 1em; }
  .select-search__item:nth-child(odd) {
    background-color: #f1f1f1; }
  .select-search__item:hover {
    background-color: #dfdfdf; }
